.cart__modal {
  margin: 130px 0 0 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.cart__modal-content {
  background-color: rgba(246, 224, 158, 0.95);
  border: 5px solid #8b4513;
  align-items: center;
  padding: 20px;
  border-radius: 8px;
  width: 100%;
  max-width: 1500px;
}

.cart__modal-section {
  width: 100%;
  margin-bottom: 20px;
}

.cart__modal-section-header {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #8b4513;
}

.cart__modal-items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  padding: 20px;
  justify-items: center;
  width: 100%;
}

.card__cart-modal {
  height: auto;
  max-height: 700px;
  min-height: 100%;
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

.card__cart-modal .card__image-container {
  height: 150px;
  overflow: hidden;
}

.card__cart-modal .card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__cart-modal .card__info {
  text-align: center;
  padding: 10px;
}

.card__cart-modal .card__title {
  font-size: 1.2rem;
}

.card__cart-modal .card__price {
  font-size: 1rem;
}

.card__cart-modal .card__actions {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.card__cart-modal .card__quantity {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

/* Media Queries */

/* Mobile View/Small Screen View */
@media screen and (max-width: 680px) {
  .cart__modal-items {
    grid-template-columns: repeat(1, 1fr);
  }
}

/*Tablet View/Laptop View */
@media screen and (min-width: 681px) and (max-width: 1024px) {
  .cart__modal-items {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Large Screen View */

@media screen and (min-width: 1025px) {
  .cart__modal-content {
    top: 2vh;
    margin-bottom: 2%;
  }
}
